<template>
  <button
    @click="toggle()"
    :class="getBtnClasses"
  >
    <i :class="getIconClasses"></i>
    {{ getText }}
  </button>

  
</template>

<script>
  export default {
    name: 'LikeBtn',
    props: {
      model: {
        type: Object,
        required: true
      },
      url: {
        type: String,
        required: true
      }
    },
    methods: {
      toggle() {
        let method = this.model.is_liked ? 'delete' : 'post';
        axios[method](this.url)
          .then((resp) => {
            this.model.is_liked = !this.model.is_liked;
            if ( method === 'post' ) {
              this.model.likes_count++;
            } else {
              this.model.likes_count--;
            }
          });
      },
    },
    computed: {
      getText() {
        return this.model.is_liked ? 'TE GUSTA' : 'ME GUSTA'
      },
      getBtnClasses() {
        return [
          this.model.is_liked ? 'font-weight-bold' : '', 
          'btn', 'btn-link', 'btn-sm', 
        ]
      },
      getIconClasses() {
        return [
          this.model.is_liked ? 'fa' : 'far', 
          'fa-thumbs-up', 'text-primary', 'mr-1', 
        ]
      }
    },
  }
</script>

<style lang="scss" scoped>
  .comments-like-btn{
    padding-left: 0;
    font-size: 0.6em;
    i {display: none;}
  }
</style>